@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,300;0,400;0,500;0,600;1,300;1,400&display=swap);

* {
  margin: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Mulish', sans-serif;
}
.navbar{
  background-color: #fff !important;
  color: #000;
}
.navbar .navbar-brand{
  color: rgb(65, 69, 129);
  font-size: 30px;
  transition: .5s;
}
.navbar .navbar-brand:hover{
  color: rgb(195, 63, 207);
}
.navbar a{
  text-transform: capitalize;
  font-size: 20px;
  color: rgb(37, 37, 37);
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid rgb(42, 117, 23);
}


/*===============*/
#header{
  width: 100%;
  height: 70vh;
  margin-top: 70px;
}

#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: rgb(32, 32, 32);
}
#header .brand-name{
  color: #3498db;
}
#header h2{
  color: #484848;
  font-size: 20px;
}
#header .btn-get-started{
  font-weight: 500;
  font-size: 20px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: .5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-started:hover{
  background: #3498db;
  color: #fff;
}
.header-img{
  text-align: right;
}

#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down {
  0%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}
@-webkit-keyframes up-down {
  0%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}


